.wizard ul.nav {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	padding-left: initial;
	padding: initial;
	background: initial;
	position: relative;
	border: initial;
	margin-bottom: 1.5rem;

	&:before {
		content: ' ';
		position: absolute;
		bottom: 1px;
		width: 100%;
		height: 1px;
		z-index: 0;
